/* Matrix Loading Screen */
.matrix-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: opacity 1s ease;
}

.matrix-canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5; /* Ensure it's above background but below content */
    pointer-events: none; /* Allow clicking through */
}

.loading-content {
    position: relative;
    z-index: 10;
    text-align: center;
    animation: pulse 2s infinite;
}

.loading-title {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 1rem;
    text-shadow: 0 0 20px #00ff00;
    animation: glitch 3s infinite;
    color: #00ff00;
}

.loading-text {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    opacity: 0.8;
    color: #00ff00;
}

.progress-bar {
    width: 300px;
    height: 4px;
    background: rgba(0, 255, 0, 0.2);
    border-radius: 2px;
    overflow: hidden;
    margin: 0 auto;
}

.progress-fill {
    height: 100%;
    background: linear-gradient(90deg, #00ff00, #00ff00, #ffffff);
    width: 0%;
    animation: loading 4s ease-in-out forwards;
    box-shadow: 0 0 10px #00ff00;
}

/* Futuristic Navigation Styles */
.nav-container {
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    padding: 10px;
    width: 100%;
    max-width: 1200px;
}

.nav-bar {
    background: rgba(15, 20, 25, 0.8);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(0, 255, 255, 0.2);
    border-radius: 50px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    gap: 30px;
    box-shadow: 
        0 8px 32px rgba(0, 255, 255, 0.1),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
    transition: all 0.3s ease;
}

.nav-bar.scrolled {
    background: rgba(15, 20, 25, 0.95);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(0, 255, 255, 0.3);
    box-shadow: 
        0 10px 30px rgba(0, 255, 255, 0.15),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.nav-bar:hover {
    transform: translateY(-2px);
    box-shadow: 
        0 12px 40px rgba(0, 255, 255, 0.2),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.logo {
    font-size: 1.8rem;
    font-weight: bold;
    background: linear-gradient(45deg, #00ffff, #0099ff, #0066ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
}

.logo:hover {
    filter: drop-shadow(0 0 10px rgba(0, 255, 255, 0.8));
    transform: scale(1.1);
}

.nav-links {
    display: flex;
    gap: 8px;
}

.nav-item {
    position: relative;
    padding: 12px 20px;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease;
    background: transparent;
    border: 1px solid transparent;
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.95rem;
    font-weight: 500;
}

.nav-item:hover {
    background: rgba(0, 255, 255, 0.1);
    border-color: rgba(0, 255, 255, 0.5);
    color: #00ffff;
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 255, 255, 0.2);
}

.nav-item.active {
    background: linear-gradient(45deg, rgba(0, 255, 255, 0.2), rgba(0, 153, 255, 0.2));
    border-color: #00ffff;
    color: #00ffff;
    box-shadow: 
        0 0 20px rgba(0, 255, 255, 0.3),
        inset 0 0 10px rgba(0, 255, 255, 0.1);
    text-shadow: 0 0 5px rgba(0, 255, 255, 0.5);
    animation: pulse-nav 2s infinite;
}

@keyframes pulse-nav {
    0%, 100% { box-shadow: 0 0 20px rgba(0, 255, 255, 0.3), inset 0 0 10px rgba(0, 255, 255, 0.1); }
    50% { box-shadow: 0 0 25px rgba(0, 255, 255, 0.4), inset 0 0 15px rgba(0, 255, 255, 0.2); }
}

.nav-item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 25px;
    background: linear-gradient(45deg, transparent, rgba(0, 255, 255, 0.1), transparent);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.nav-item:hover::before {
    opacity: 1;
}

/* About Section Matrix Style */
.matrix-about {
    background: linear-gradient(135deg, #0f1419 0%, #1a2332 50%, #0f1419 100%);
    border: 1px solid rgba(0, 255, 255, 0.3);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 
        0 8px 32px rgba(0, 255, 255, 0.1),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
}

.matrix-about::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at center, transparent 30%, rgba(0, 255, 255, 0.03) 70%);
    animation: pulse-glow 8s infinite;
    z-index: 0;
    pointer-events: none;
}

.matrix-about::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 20px;
    box-shadow: inset 0 0 30px rgba(0, 255, 255, 0.1);
    z-index: 1;
    pointer-events: none;
}

.matrix-about > * {
    position: relative;
    z-index: 2;
}

.matrix-about h2 {
    background: linear-gradient(45deg, #00ffff, #0099ff, #0066ff);
    background-size: 200% auto;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    text-align: center;
    animation: shimmer 3s linear infinite;
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
}

@keyframes pulse-glow {
    0%, 100% { opacity: 0.3; transform: scale(1); }
    50% { opacity: 0.5; transform: scale(1.05); }
}

.role-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
    margin-top: 40px;
}

.role-card {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(0, 255, 255, 0.2);
    border-radius: 20px;
    padding: 40px 30px;
    text-align: center;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    position: relative;
    overflow: hidden;
}

.role-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, 
        transparent, 
        rgba(0, 255, 255, 0.2), 
        rgba(0, 255, 255, 0.4), 
        rgba(0, 255, 255, 0.2), 
        transparent
    );
    transform: translateX(-100%);
    animation: slide-right 3s ease-in-out infinite;
    animation-delay: calc(var(--card-index, 0) * 1s);
}

.role-card::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, 
        transparent, 
        rgba(0, 255, 255, 0.2), 
        rgba(0, 255, 255, 0.4), 
        rgba(0, 255, 255, 0.2), 
        transparent
    );
    transform: translateX(100%);
    animation: slide-left 3s ease-in-out infinite;
    animation-delay: calc(var(--card-index, 0) * 1s);
}

.role-card:hover {
    transform: translateY(-10px);
    border-color: rgba(0, 255, 255, 0.5);
    box-shadow: 
        0 20px 40px rgba(0, 255, 255, 0.1),
        0 0 15px rgba(0, 255, 255, 0.2);
}

.role-card:hover .role-icon {
    box-shadow: 0 0 30px rgba(0, 255, 255, 0.6);
    transform: scale(1.1);
}

.role-icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 20px;
    background: linear-gradient(45deg, #00ffff, #0099ff);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    animation: float 3s ease-in-out infinite;
    transition: all 0.3s ease;
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.3);
}

@keyframes slide-right {
    0%, 100% { transform: translateX(-100%); }
    50% { transform: translateX(100%); }
}

@keyframes slide-left {
    0%, 100% { transform: translateX(100%); }
    50% { transform: translateX(-100%); }
}

.role-card:nth-child(2) .role-icon { animation-delay: -1s; }
.role-card:nth-child(3) .role-icon { animation-delay: -2s; }

/* Animations */
@keyframes pulse {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.7; }
}

@keyframes glitch {
    0%, 90%, 100% { transform: translateX(0); }
    10% { transform: translateX(-2px); }
    20% { transform: translateX(2px); }
    30% { transform: translateX(-2px); }
    40% { transform: translateX(2px); }
    50% { transform: translateX(-2px); }
    60% { transform: translateX(2px); }
    70% { transform: translateX(-2px); }
    80% { transform: translateX(2px); }
}

@keyframes loading {
    0% { width: 0%; }
    10% { width: 15%; } /* Slower at start for better effect */
    50% { width: 50%; }
    75% { width: 75%; }
    100% { width: 100%; }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes shimmer {
    0% { background-position: -200% center; }
    100% { background-position: 200% center; }
}

@keyframes float {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
}

/* Responsive */
@media (max-width: 768px) {
    .nav-container {
        width: 90%;
    }
    
    .nav-bar {
        flex-direction: column;
        gap: 15px;
        padding: 15px 20px;
    }

    .nav-links {
        flex-wrap: wrap;
        justify-content: center;
    }

    .role-cards {
        grid-template-columns: 1fr;
    }
}